<script>
import HomeCalendarItem from '@components/_CoreTaxes/Calendar/HomeCalendarItem.vue'
import { GET_PERIODS } from '@src/apollo/queries'
// import { mount } from '@vue/test-utils'
import GxCardModal from '@src/components/_Core/Cards/GxCardModal'
// import { CONFIG_SET_BEGINNING_PERIOD } from '@src/apollo/mutations'

export default {
  name: 'HomeCalendar',
  data() {
    return {
      searchPeriods: true,
      year: new Date().getFullYear(),
      months: [
        {
          periodId: Number(this.year + '01'),
          month: 1,
          name: 'Enero',
          status: '',
          validationStatus: '',
        },
        {
          periodId: Number(this.year + '02'),
          month: 2,
          name: 'Febrero',
          status: '',
          validationStatus: '',
        },
        {
          periodId: Number(this.year + '03'),
          month: 3,
          name: 'Marzo',
          status: '',
          validationStatus: '',
        },
        {
          periodId: Number(this.year + '04'),
          month: 4,
          name: 'Abril',
          status: '',
          validationStatus: '',
        },
        {
          periodId: Number(this.year + '05'),
          month: 5,
          name: 'Mayo',
          status: '',
          validationStatus: '',
        },
        {
          periodId: Number(this.year + '06'),
          month: 6,
          name: 'Junio',
          status: '',
          validationStatus: '',
        },
        {
          periodId: Number(this.year + '07'),
          month: 7,
          name: 'Julio',
          status: '',
          validationStatus: '',
        },
        {
          periodId: Number(this.year + '08'),
          month: 8,
          name: 'Agosto',
          status: '',
          validationStatus: '',
        },
        {
          periodId: Number(this.year + '09'),
          month: 9,
          name: 'Septiembre',
          status: '',
          validationStatus: '',
        },
        {
          periodId: Number(this.year + '10'),
          month: 10,
          name: 'Octubre',
          status: '',
          validationStatus: '',
        },
        {
          periodId: Number(this.year + '11'),
          month: 11,
          name: 'Noviembre',
          status: '',
          validationStatus: '',
        },
        {
          periodId: Number(this.year + '12'),
          month: 12,
          name: 'Diciembre',
          status: '',
          validationStatus: '',
        },
      ],
      periods: null,
      beginningPeriod: null,
      clientNumber: null,
      accountingFirmNumber: null,
      errorDescription: null,
    }
  },
  // props: {
  //   clientNumber: {
  //     type: Number,
  //     default: null,
  //     required: false,
  //   },
  //   accountingNumber: {
  //     type: String,
  //     default: null,
  //     required: false,
  //   },
  // },
  apollo: {
    myAccountingFirms: {
      query: GET_PERIODS,
      variables() {
        return {
          clientNumber: [localStorage.getItem('clientNumber')],
          year: this.year,
        }
      },
      fetchPolicy: 'no-cache',
    },
  },
  watch: {
    myAccountingFirms(newVal) {
      if (newVal) {
        this.clientNumber = newVal[0].clients[0].clientNumber
        this.accountingFirmNumber = newVal[0].accountingFirmNumber
        // console.log(this.clientNumber)
        // console.log(this.accountingFirmNumber)
        // if (this.searchPeriods)
        //   if (newVal[0].clients[0].accounting.periods.length === 0) {
        //     this.year--
        //     return
        //   }
        // this.searchPeriods = false
        this.periods = newVal[0].clients[0].accounting.periods

        this.beginningPeriod = newVal[0].clients[0].accounting.beginningPeriod
        // .toString()
        // .substring(0, 4)
        this.resetMonths()
        this.fillMonths()
      }
    },
    immediate: true,
  },
  components: { HomeCalendarItem, GxCardModal },
  computed: {
    enableNext() {
      return this.year < new Date().getFullYear() ? true : false
    },
    beginningYear() {
      return this.beginningPeriod.toString().substring(0, 4)
    },
    enableBack() {
      return Number(this.beginningYear) < this.year ? true : false
    },
  },
  methods: {
    nextYear() {
      if (this.enableNext) this.year++
    },
    backYear() {
      if (this.enableBack) this.year--
    },
    resetMonths() {
      this.months = [
        {
          periodId: Number(this.year + '01'),
          month: 1,
          name: 'Enero',
          status: '',
          validationStatus: '',
        },
        {
          periodId: Number(this.year + '02'),
          month: 2,
          name: 'Febrero',
          status: '',
          validationStatus: '',
        },
        {
          periodId: Number(this.year + '03'),
          month: 3,
          name: 'Marzo',
          status: '',
          validationStatus: '',
        },
        {
          periodId: Number(this.year + '04'),
          month: 4,
          name: 'Abril',
          status: '',
          validationStatus: '',
        },
        {
          periodId: Number(this.year + '05'),
          month: 5,
          name: 'Mayo',
          status: '',
          validationStatus: '',
        },
        {
          periodId: Number(this.year + '06'),
          month: 6,
          name: 'Junio',
          status: '',
          validationStatus: '',
        },
        {
          periodId: Number(this.year + '07'),
          month: 7,
          name: 'Julio',
          status: '',
          validationStatus: '',
        },
        {
          periodId: Number(this.year + '08'),
          month: 8,
          name: 'Agosto',
          status: '',
          validationStatus: '',
        },
        {
          periodId: Number(this.year + '09'),
          month: 9,
          name: 'Septiembre',
          status: '',
          validationStatus: '',
        },
        {
          periodId: Number(this.year + '10'),
          month: 10,
          name: 'Octubre',
          status: '',
          validationStatus: '',
        },
        {
          periodId: Number(this.year + '11'),
          month: 11,
          name: 'Noviembre',
          status: '',
          validationStatus: '',
        },
        {
          periodId: Number(this.year + '12'),
          month: 12,
          name: 'Diciembre',
          status: '',
          validationStatus: '',
        },
      ]
    },
    fillMonths() {
      //console.log(this.periods)
      this.periods.forEach((period) => {
        let index = this.months.findIndex(
          (m) =>
            m.month ===
            Number(period.periodId.replace(this.year.toString(), ''))
        )
        //console.log(index)
        this.months[index].status = period.status
        this.months[index].validationStatus = period.validationStatus
        // this.months[index].periodId = period.periodId
      })
      //console.log(this.months)
    },
    disabledMonth(month) {
      let periodId = Number(
        this.year + (month > 9 ? month : '0' + month).toString()
      )
      //console.log(Number(this.beginningPeriod), periodId)
      return Number(this.beginningPeriod) > periodId
    },
    showError(error) {
      this.errorDescription = error
    },
    closeModel() {
      this.errorDescription = ''
    },
  },
}
</script>

<template>
  <div
    v-if="myAccountingFirms"
    class="flex flex-col min-h-0 w-50 rounded-lg shadow-2 pl-2 border w-full max-w-2xl bg-calendar-background"
  >
    <div
      class="flex flex-row items-stretch text-black content-center max-w-2xl text-xl mt-3"
    >
      <div class="flex-1 text-right mt-2px"
        ><GxIcon
          :icon="'keyboard_arrow_left'"
          class="cursor-pointer mr-2"
          :class="{
            'text-gray-300': !enableBack,
            'text-gray-black': enableBack,
          }"
          :title="'Settings'"
          v-bind:style="{ fontSize: '25px' }"
          @click="backYear"
      /></div>
      <div class="text-center font-bold">
        {{ year }}
      </div>
      <div class="flex-1 text-left mt-2px"
        ><GxIcon
          :icon="'keyboard_arrow_right'"
          class="cursor-pointer ml-2"
          :class="{
            'text-gray-300': !enableNext,
            'text-gray-black': enableNext,
          }"
          :title="'Settings'"
          v-bind:style="{ fontSize: '25px' }"
          @click="nextYear"
      /></div>
    </div>
    <div class="flex content-start flex-wrap  max-w-3xl pb-7">
      <!-- <div class="flex 1" > -->
      <HomeCalendarItem
        v-for="month in months"
        :key="month.month"
        :disabled="disabledMonth(month.month)"
        :month="month"
        :accountingFirmNumber="accountingFirmNumber"
        :clientNumber="clientNumber"
        @showError="showError"
      />
      <!-- </div> -->
    </div>
    <GxCardModal
      v-if="errorDescription"
      :headline="'Error al abrir el periodo'"
      :caption="errorDescription"
      :card-img="$utils.alertImages.Oops"
      alt="Error"
      delayAnimation
    >
      <template v-slot:actions>
        <div class="flex justify-center mt-4">
          <GxButton @click="closeModel">ACEPTAR</GxButton>
        </div>
      </template>
    </GxCardModal>
  </div>
</template>
