<script>
import { PERIOD_OPEN } from '@src/apollo/mutations'

export default {
  name: 'HomeCalendarItem',
  data() {
    return {
      hover: false,
      showOpenPeriod: false,
      errorDescription: '',
    }
  },
  props: {
    month: {
      type: Object,
      default: null,
    },
    disabled: {
      type: Boolean,
      default: false,
    },
    clientNumber: {
      type: String,
      default: null,
    },
    accountingFirmNumber: {
      type: String,
      default: null,
    },
  },
  computed: {
    getStatusName() {
      if (this.month.status === 'open') return 'Abierto'
      if (this.month.status === 'closed') return 'Cerrado'
      return ''
    },
    getValidationStatus() {
      // console.log(this.month.validationStatus)
      if (
        this.month.validationStatus === 'nothingPending' ||
        this.month.validationStatus === ''
      )
        return false
      return true
    },
    getStatusNotStarted() {
      return !this.showOpenPeriod && !this.getStatusName
    },
  },
  methods: {
    mouseOver(type) {
      if (this.getStatusName && !this.disabled) this.hover = type
      if (!this.getStatusName && !this.disabled) this.showOpenPeriod = type
    },
    mouseOut(type) {
      this.hover = type
      this.showOpenPeriod = type
    },
    async onClick() {
      if (this.month.status === 'open' || this.month.status === 'closed') {
        //console.log('goToMonth')
        localStorage.setItem('periodDefault', this.month.periodId)
        this.$router.push({
          name: 'journalEntriesView',
          params: { periodId: this.month.periodId },
        })
      }
      // else {
      //   if (!this.disabled) {
      //     this.createPeriod()
      //   }
      // }
    },
    async createPeriod() {
      if (!this.disabled) {
        try {
          const response = await this.$apollo.mutate({
            mutation: PERIOD_OPEN,
            variables: {
              accountingFirmNumber: this.accountingFirmNumber,
              clientNumber: this.clientNumber,
              period: this.month.periodId,
            },
          })

          if (
            response.data.periodOpen.error !== null &&
            response.data.periodOpen.period === null
          ) {
            this.errorDescription =
              response.data.periodOpen.error.message +
              ' - ' +
              response.data.periodOpen.error.details.join('')
            this.$emit('showError', this.errorDescription)
          } else {
            this.showOpenPeriod = false
            this.month.status = response.data.periodOpen.period.status
            this.month.validationStatus =
              response.data.periodOpen.period.validationStatus
          }
        } catch (error) {
          this.$emit('showError', this.error)
        }
      }
    },
  },
}
</script>

<template>
  <div
    class="relative h-30 w-37 border p-3 ml-3 mb-4 rounded-lg shadow-2 cursor-pointer bg-white"
    :class="{ 'hover:bg-gray-200 hover:shadow-3': !this.disabled }"
    @mouseover="mouseOver(true)"
    @mouseout="mouseOver(false)"
    @click="onClick"
  >
    <div class="flex justify-between text-sm">
      <div
        class="flex flex-col"
        :class="{
          'text-black font-bold': !getStatusNotStarted,
          'text-gray-500': getStatusNotStarted,
        }"
      >
        {{ month.name }}
      </div>
      <div class="flex flex-col pt-px"
        ><GxIcon
          v-show="this.hover"
          :icon="'launch'"
          class="cursor-pointer ml-2 text-gray-500"
          :title="'launch'"
          v-bind:style="{ fontSize: '16px' }"
      /></div>
    </div>
    <div
      v-if="getStatusName"
      class="mt-1 flex fixed text-2xs rounded-full pl-1 pt-px pb-px pr-1 font-bold"
      :class="{
        'bg-gray-200 text-gray-500': this.month.status === 'closed',
        'bg-success text-white': this.month.status === 'open',
      }"
    >
      {{ getStatusName }}
    </div>

    <div
      v-if="getValidationStatus"
      class="flex flex-col w-full absolute bottom-0 pb-3 items-end pr-6"
    >
      <GxIcon
        :icon="'flag'"
        class="cursor-pointer ml-2 text-pending"
        :title="'launch'"
        v-bind:style="{ fontSize: '16px' }"
      />
    </div>

    <div
      v-if="this.showOpenPeriod"
      class="flex flex-col w-full absolute bottom-0 items-center text-center"
      @click="createPeriod"
    >
      <span
        class="flex flex-col mr-5 mb-4 h-6 w-28 pt-px pb-px border text-sm rounded-lg text-primary font-bold border-primary"
      >
        Iniciar período</span
      >
    </div>

    <div
      v-if="getStatusNotStarted && !this.disabled"
      class="flex flex-col w-full absolute bottom-0 text-right"
    >
      <span class="flex flex-col mr-5 mb-2 text-2xs font-bold text-gray-500"
        >No iniciado</span
      >
    </div>
  </div>
</template>
